@import '../colourTheme.css';

.sponsorsContainer {
    min-height: 80vh;
    padding: 3vh 2vw 1vh 2vw;
    background: white;
    text-align: center;
    /* align-content: center; */
}

.sponsorsContainer h1 {
    margin-bottom: 5vh;
    margin-top: 7vh;
}

.sponsHeading {
    font-size: 3rem;
    font-weight: bold;
}

.sponsorTable {
    width: 100%;
}

table {
    border-collapse: collapse;
    width: 80%;
}

td {
    text-align: center;
    /* border: 5px solid black; */
}

.sponsLogo {
    max-width: 150px;
    border-radius: 50%;
    width: 20vw;
    margin-top: 3vw;
    margin-bottom: 1.5vw;
    transition: transform .5s;
}

.sponsName {
    font-size: 1.5rem;;
}


.placeHolder {
    font-size: 1.5rem;
}



