.placeHolder {
    display:inline-block;
    transition: transform .5s;
}



.diamondSponsor{
    padding-top: 2vh;
    width: 100%;
    margin-bottom: 8vh;
}
/* .diamondSponsor h1{
    color: var(--diamond-back);
} */
.diamondSponsor .sponsorTable .sponsLogo:hover {
    transform: scale(1.1);
    /* -webkit-border-radius: 10px;
    -moz-border-radius: 10px; */
    border-radius: 50%;
    /* -webkit-box-shadow: 0px 0px 30px 0px rgba(0, 255, 0, 0.67);
    -moz-box-shadow:    0px 0px 30px 0px rgba(0, 255, 0, 0.67); */
    box-shadow:         0px 0px 30px 0px var(--diamond);
}
.diamondSponsor .sponsorTable .placeHolder:hover {
    transform: scale(1.1);
    /* -webkit-border-radius: 10px;
    -moz-border-radius: 10px; */
    border-radius: 25px;
    /* -webkit-box-shadow: 0px 0px 30px 0px rgba(0, 255, 0, 0.67);
